@import url(https://fonts.googleapis.com/css2?family=Cabin:ital,wght@0,400;0,500;0,600;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
* {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

body {
  margin: 0;
  font-family: 'Cabin', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
    min-width: 100%;
    width: 100%;
    max-width: 100%;
    min-height: 100vh;
    background-size: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    background-color: rgb(195, 191, 188);
}
.Title-container {
    margin: 25px 25px 0 25px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 1200px;
}

.Title-container div {
    flex: 1 1;
    margin-top: 20px;
    margin-bottom: auto;
}

.Title-container hr {
    min-width: 50px;
    width: calc(100% - 20px);
    margin: 2px 10px;
    border-width: 2px;
}

.Title-container h1 {
    max-width: 310px;
    margin: 0 10px;
    font-size: 42px;
    font-weight: 700;
    font-style: italic;
    color: rgb(54, 51, 155);
}

.Title-container h1 span {
    display: inline-block;
}

.Title-container h1 b {
    letter-spacing: -4px;
}

@media(max-width: 520px) {
    .Title-container h1 {
        font-size: 36px;
        width: 258px;
    }
}

@media(max-width: 470px) {
    .Title-container h1 {
        font-size: 33px;
        width: 234px;
    }
}

@media(max-width: 400px) {
    .Title-container h1 {
        font-size: 27px;
        width: 185px;
    }
}

@media(max-width: 350px) {
    .Title-container h1 {
        font-size: 24px;
        width: 160px;
    }
}
.Upload {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Upload-container {
    background-color: rgb(132,130,135);
    width: 318px;
    max-width: calc(100% - 100px);
    cursor: pointer;
    transition: opacity 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 25px 25px 10px 25px;
    padding: 25px;
    border: 3px dashed black;
    border-radius: 10px 10px 40px 10px;
    position: relative;
}

.Upload-container input {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    border: 3px solid black;
    outline: none;
    opacity: 0;
    cursor: pointer;
}

.Upload-container img {
    pointer-events: none;
    height: 42px;
    margin: 8px;
}

.Upload-container h3 {
    pointer-events: none;
    margin: 8px;
    text-align: center;
}

.Upload-container-dragInside {
    opacity: 0.7;
}

.Upload-error {
    text-align: center;
    color:#a8020b;
    background-color:rgba(216, 0, 12, 0.2);
    padding: 0;
    margin: 0 25px;
    border: 3px solid rgba(216, 0, 12, 0);
    height: 0;
    transition: opacity 0.01s ease-in-out;
}

@media(hover:hover) {
    .Upload-container:hover {
        opacity: 0.8;
    }
}
.Edit-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 25px;
}

.Edit-content {
    display: flex;
    align-items: center;
    justify-content: center;
}

.Edit-images {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(132,130,135);
    border-radius: 15px 15px 50px 15px;
    padding: 15px 30px 15px 25px;
}

@media(max-width: 940px) {
    .Edit-container {
        margin: 25px 25px 0 25px;
    }
    .Edit-content {
        flex-direction: column;
    }
}

@media(max-width: 740px) {
    .Edit-images {
        flex-direction: column;
    }
}

@media(max-width: 390px) {
    .Edit-images {
        background-color: transparent;
        border-radius: 0;
        padding: 0;
    }
}
.Edit-uploaded {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px;
    max-width: 300px;
}

.Edit-uploaded-pic {
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    max-width: 296px;
    max-height: 296px;
    border-top: 2px solid rgb(128, 128, 128);
    border-left: 2px solid rgb(128, 128, 128);
    border-right: 2px solid rgb(64, 64, 64);
    border-bottom: 2px solid rgb(64, 64, 64);
    box-shadow: 2px 2px 0 black ;
}

.Edit-uploaded-button {
    font-family: 'Cabin', sans-serif;
    font-family: 'Press Start 2P', cursive;
    font-size: 12px;
    color: rgba(255,255,255,0.6);
    background-color: rgb(176, 42, 111);
    border-radius: 1000px;
    border: none;
    border-bottom: 3px solid rgb(109, 27, 70);
    max-width: 300px;
    min-width: 150px;
    height: 39px;
    cursor: pointer;
    outline: none;
    padding: 3px 0 0 0;
    margin: 10px 0 0 1px;
}

.Edit-uploaded-button:active {
    border: none;
    height: 36px;
    margin-top: 13px;
}    
#Edit-properties {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px 0 10px 25px;
}

#Edit-properties h3 {
    color: rgb(54, 51, 155);
    font-size: 15px;
    max-width: 150px;
    text-align: center;
    margin: 0 0 25px 15px;
    font-family: 'Press Start 2P', cursive;
}

#Edit-properties-properties {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Edit-property {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 5px;
}

.Edit-property img {
    width: 36px;
    margin: 6px 20px 6px 6px;
}

.Edit-arrows {
    width: 72px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform: rotate(-30deg)
}

.Edit-arrows-buttons {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    border-radius: 1000px;
    padding: 3px 6px;
}

.Edit-arrow {
    width: 30px;
    height: 30px;
    background-color: rgb(176, 42, 111);
    border-radius: 1000px;
    border: none;
    cursor: pointer;
    margin: 0;
    outline: none;
}

.Edit-arrow-disabled {
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    cursor: default;
    opacity: 0.5;
}

.Edit-arrows-tags {
    color: rgb(54, 51, 155);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Edit-arrows-tags p {
    line-height: 15px;
    width: 30px;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: 30px;
    font-weight: 700;
    text-align: center;
}

@media(max-width: 940px) {
    #Edit-properties {
        margin: 25px 25px 0 25px;
    }
    #Edit-properties h3 {
        max-width: 100%;
        margin: 5px 15px 25px 15px !important;
    }
    #Edit-properties-properties {
        flex-direction: row;
        padding-left: 5px;
    }
    #Edit-properties h3 {
        margin: 0;
    }
    .Edit-property {
        margin: 20px;
    }
    .Edit-property img {
        width: 36px;
        margin: 6px 15px 25px 6px;
        padding-right: 10px;
    }
}

@media(max-width: 540px) {
    .Edit-property {
        margin: 0 6vw;
        flex-direction: column;
    }
    .Edit-property img {
        width: 36px;
        margin: 6px 6px 25px 6px;
        padding-right: 10px;
    }
}
.Edit-output {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px;
    max-width: 300px;
}

.Edit-output-canvas {
    max-width: 296px;
    max-height: 296px;
    border-top: 2px solid rgb(128, 128, 128);
    border-left: 2px solid rgb(128, 128, 128);
    border-right: 2px solid rgb(64, 64, 64);
    border-bottom: 2px solid rgb(64, 64, 64);
    box-shadow: 2px 2px 0 black;
}

.Edit-output-button {
    text-decoration: none;
    text-align: center;
    color: rgba(255,255,255,0.6);
    font-family: 'Press Start 2P', cursive;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    background-color: rgb(176, 42, 111);
    border-radius: 1000px;
    border: none;
    border-bottom: 3px solid rgb(109, 27, 70);
    max-width: 300px;
    min-width: 150px;
    height: 33px;
    cursor: pointer;
    outline: none;
    padding-top: 3px;
    margin: 10px 0 0 1px;
}

.Edit-output-button:active {
    border: none;
    margin-top: 13px;
}    
.Body-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Body-credits {
    font-weight: 500;
    font-size: 16px;
    margin: 10px;
    text-align: center;
}

.Body-credits a {
    transition: 0.2s ease-in-out;
}

.Body-data {
    margin: 10px 10px 25px 10px;
    text-align: center;
}

@media(hover:hover) {
    .Body-credits a:hover {
        opacity: 0.65;
    }
}
.Explanation-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 3vh 0 0 0;
}

.Explanation-container h3 {
    font-size: 24px;   
    margin: 10px;
    text-align: center;
}

.Explanation-container h3 b {
    font-weight: 700;
    font-style: italic;
    letter-spacing: -2px;
}

.Explanation-container p {
    font-size: 18px;
    font-weight: 400;
    margin: 10px;
}

.Explanation-container p b {
    font-weight: 700;
    letter-spacing: -2px;
}

.Explanation-text {
    margin: 25px;
    max-width: 423px;
}

.Explanation-does {
    margin: 10px 0;
}

.Explanation-works {
    margin: 10px 0;
}

.Explanation-looks {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px;
    background-color: rgb(132,130,135);
    border-radius: 15px 15px 50px 15px;
    padding: 25px;
}

.Explanation-looks-title {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.Explanation-looks-title div {
    flex: 1 1;
}

.Explanation-looks-title div hr {
    min-width: 50px;
    width: 100%;
    margin: 2px 0 13px 0;
}

.Explanation-looks h3 {
    margin-top: 0;
    font-size: 21px;
}

.Explanation-looks-inner {
    display: flex;
    align-items: center;
    justify-content: center;
}

.Explanation-looks img {
    margin: 10px;
    height: 181px;
    border-top: 2px solid rgb(128, 128, 128);
    border-left: 2px solid rgb(128, 128, 128);
    border-right: 2px solid rgb(64, 64, 64);
    border-bottom: 2px solid rgb(64, 64, 64);
    box-shadow: 2px 2px 0 black ;
}

#Explanation-looks-arrow {
    transform: rotate(180deg);
    width: 15px;
    height: 25px;
    border: none;
    box-shadow: none;
}

@media(max-width: 866px) {
    .Explanation-container {
        flex-direction: column;
        margin: 0;
    }
}

@media(max-width: 500px) {
    .Explanation-looks-inner {
        flex-direction: column;
    }
    #Explanation-looks-arrow {
        transform: rotate(270deg);
    }
}

@media(max-width: 380px) {
    .Explanation-looks h3 {
        font-size: 17px;
    }
    .Explanation-looks-title div hr {
        min-width: 40px;
        margin: 3px 0 13px 0;
    }
}
.Share-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 25px;
}

.Share-container h3 {
    font-size: 24px;
    margin: 10px 25px;
    text-align: center;
}

.Share-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
}

.Share-buttons a {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
    border-radius: 1000px;
    padding: 5px;
    width: 24px;
    height: 24px;
    transition: 0.3s ease-in-out;
    background-color: rgb(176, 42, 111);
}

.Share-buttons a img {
    max-height: 80%;
    max-width: 80%;
}

@media(max-width: 350px) {
    .Share-container h3 {
        font-size: 21px;
        margin: 15px 25px;
    }
}

@media(hover:hover) {
    .Share-buttons a:hover {
        transform: translateY(-3px);
    }
}
